import { within, userEvent } from '@storybook/testing-library';
import { Page } from './Page';
export default {
  title: 'Example/Page',
  component: Page,
  parameters: {
    // More on how to position stories at: https://storybook.js.org/docs/7.0/react/configure/story-layout
    layout: 'fullscreen'
  }
};
export const LoggedOut = {};

// More on interaction testing: https://storybook.js.org/docs/7.0/react/writing-tests/interaction-testing
export const LoggedIn = {
  play: async ({
    canvasElement
  }) => {
    const canvas = within(canvasElement);
    const loginButton = await canvas.getByRole('button', {
      name: /Log in/i
    });
    await userEvent.click(loginButton);
  }
};
LoggedOut.parameters = {
  ...LoggedOut.parameters,
  storySource: {
    source: "{}",
    ...LoggedOut.parameters?.storySource
  }
};
LoggedIn.parameters = {
  ...LoggedIn.parameters,
  storySource: {
    source: "{\n  play: async ({\n    canvasElement\n  }) => {\n    const canvas = within(canvasElement);\n    const loginButton = await canvas.getByRole('button', {\n      name: /Log in/i\n    });\n    await userEvent.click(loginButton);\n  }\n}",
    ...LoggedIn.parameters?.storySource
  }
};